import { Auth0ContextInterface, User } from "@auth0/auth0-react"
import { useEffect, useState } from "react"

export const ExternalPage = ({ path, auth }: ExternalPageProps) => {

    const [page, setPageData] = useState<any>()
    const fetchPage = () => {
        if (auth.isAuthenticated) {
            auth.getAccessTokenSilently()
                .then(token => {
                    return fetch(path, {
                        headers: new Headers({
                            'Authorization': 'Bearer ' + token,
                            'Content-Type': 'application/x-www-form-urlencoded'
                        }),
                    })
                })
                .then(response => {
                    return response.json()
                })
                .then(data => {
                    console.log(data);
                    return setPageData(data)
                })
        } else {
            return fetch('/')
        }

    }

    useEffect(() => {
        if (!page) {
            fetchPage()
        }
    }, [page, fetchPage])

    return (
        <div>{JSON.stringify(page)}</div>
    );
}
export interface ExternalPageProps {
    path: string;
    auth: Auth0ContextInterface<User>;
}