import { Auth0ContextInterface, User } from "@auth0/auth0-react"
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Unstable_Grid2'
import { Item } from "./Item";

function getUserRole(user?: User): string {
    if (user) {
        return user["http://naja.dev/groups"].map((x: string) => {
            return x.charAt(0).toUpperCase() + x.slice(1);
        }).join(", ")
    }
    return "N/A"
}

export default function UserDetails(auth: Auth0ContextInterface) {
    if (auth.user) {
        return (
            <Item>
                {UserDetailTextField("First Name", auth.user.given_name)}
                {UserDetailTextField("Last Name", auth.user.family_name)}
                {UserDetailTextField("Email", auth.user.email)}
                {UserDetailTextField("Groups", getUserRole(auth.user))}
            </Item>
        );
    }
}

function UserDetailTextField(name: string, value: string | undefined) {
    const id = `${name.toLowerCase()}-detail`;
    if (value) {
        return (
            <Grid xs={12} >
                <TextField
                    id={id}
                    label={name}
                    defaultValue={value}
                    InputProps={{
                        readOnly: true,
                    }}
                    variant="standard"
                />
            </Grid>
        )
    } else {
        return (
            <Grid xs={12}>
                <TextField
                    disabled
                    id={id}
                    label={name}
                    defaultValue="N/A"
                    variant="standard"
                />
            </Grid>
        )
    }
}